import {UniAudio} from 'components/uni/uni-audio/uni-audio'

export default{
	components:{
		UniAudio
	},
	props:{
	      /**题目类别 */
	      typeName:String,
	      /**题目标题 */
	      title:String,
	      /**是否是听力题 */
	      is_hearing:null,
	      /**听力的路径 */
	      audiofile:String,
		  
	    },
		
		data(){
			return {
				src:'',
				play:false
			}
		},
	
		created() {
			
			this.$store.ready(state=>{
				this.src = state.SRC
			})
		}
}
